import qs from "qs";
import * as recompose from "recompose";
import Redirect from "components/foundational/Redirect";
import { formatUrl } from "utils/formatting";

const withRedirectUrl = recompose.withProps(({ location }) => ({
  redirectUrl: formatUrl(
    qs.parse(location.search, { ignoreQueryPrefix: true }).redirect_to || "/",
  ),
}));

const withRedirectOnMount = recompose.compose(
  withRedirectUrl,
  recompose.lifecycle({
    componentDidMount() {
      setTimeout(() => {
        window.location.replace(this.props.redirectUrl);
      }, 1200);
    },
  }),
);

export default recompose.compose(withRedirectOnMount)(Redirect);
